export default class Repulsion {
    constructor(canvas, context, x, y) {
        this.x = x;
        this.y = y;
        this.ox = x;
        this.oy = y;
        this.canvas = canvas;
        this.context = context;
        this.size = 5 || Math.random() * 5 + 1;
        this.diameter = this.size * 2;
        this.cols = parseInt(this.canvas.width / this.diameter);
        this.rows = parseInt(this.canvas.height / this.diameter);
        this.maxDistance = 50;
    }
    draw() {
        this.context.fillColor= 'red';
        this.context.beginPath().createArc(this.x, this.y, this.size, 0, 360).fill().closePath();
    }
    update(offsetX,offsetY) {
        let dx = this.x - offsetX;
        let dy = this.y - offsetY;
        let r = Math.sqrt(dx * dx + dy * dy);
        let fx = dx / r;
        let fy = dy / r;
        if (r <= this.maxDistance ) {
            this.x += fx*0.5;
            this.y += fy*0.5;
        } else {
            let dx1 = this.x - this.ox;
            let dy1 = this.y - this.oy;
            let r0 = Math.sqrt(dx1 * dx1 + dy1 * dy1);
            if (r0 != 0) {
                this.x -= (dx1/r0)*0.5;
                this.y -= (dy1/r0)*0.5;
            }
        }
    }
}