<template>
  <div>
    <canvas width="600" height="600" ref="test" />
  </div>
</template>
<script>
import Repulsion from "../service/repulsion";
import Canvas from "../service/canvas";

export default {
  mounted() {
    this.canvas = new Canvas();
    this.context = this.canvas.createCanvas(this.$refs.test, "2d");
    this.board = this.$refs.test;
    this.$refs.test.addEventListener("mousemove", (e) => {
      this.xPos = e.offsetX;
      this.yPos = e.offsetY;
    });
    for (let i = this.size; i < this.board.width; i += this.size * 2) {
      for (let j = this.size; j < this.board.height; j += this.size * 2) {
        this.Particles.push(new Repulsion(this.board, this.context, i, j));
      }
    }
    this.RepulsionAnimation();
  },
  data() {
    return {
      canvas: null,
      context: null,
      size: 5,
      Particles: [],
      board: "",
      xPos: 1000,
      yPos: 1000,
    };
  },
  methods: {
    RepulsionAnimation() {
      this.context.clearRect(0,0,this.board.width,this.board.height);
      this.Particles.forEach((d) => {
        d.draw();
        d.update(this.xPos, this.yPos);
      });
      requestAnimationFrame(this.RepulsionAnimation);
    },
  },
};
</script>